import React from 'react'
import PropTypes from 'prop-types'
import { useTheme, createUseStyles } from 'react-jss'
import cx from 'classnames'

const useStyles = createUseStyles((theme) => ({
  picker: {
    maxWidth: 210,
  },
  preview: {
    width: '100%',
    height: 90,
    marginBottom: 22,
    border: [3, 'solid', theme.colors.brand.background],
    borderRadius: 16,
    boxShadow: [0, 0, 3, 0, 'rgba(0, 0, 0, 0.12)'],
  },
  property: {
    display: 'block',
    marginBottom: 8,
    padding: 6,
    border: 'none',
    borderRadius: 6,
    backgroundColor: theme.colors.grayscale[100],
    '&:focus': {
      outline: 'none',
    },
  },
}))

const ThemeColorPicker = ({ color, name, className }) => {
  const theme = useTheme()
  const styles = useStyles(theme)

  return (
    <div className={cx(styles.picker, className)}>
      <div className={styles.preview} style={{ backgroundColor: color }}></div>
      <input className={styles.property} value={color} onChange={() => {}} />
      <span className={styles.property}>{name}</span>
    </div>
  )
}

ThemeColorPicker.propTypes = {
  color: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  className: PropTypes.string,
}

export default ThemeColorPicker
