import React from 'react'
import { useTheme, createUseStyles } from 'react-jss'

import Layout from '../components/Layout'
import Sidebar from '../components/Sidebar'
import Main from '../components/Main'
import SEO from '../components/SEO'
import ThemeColorPicker from '../components/theming/ThemeColorPicker'

const useStyles = createUseStyles((theme) => ({
  '@global': {
    h3: {
      color: theme.colors.grayscale[400],
    },
  },
  callout: {
    padding: 24,
    backgroundColor: theme.colors.grayscale[100],
    borderRadius: 6,
    '& h1': {
      marginTop: 0,
      marginBottom: 16,
    },
    '& p': {
      margin: 0,
    },
  },
  colors: {
    display: 'flex',
    justifyContent: 'space-between',
    listStyle: 'none',
    margin: 0,
    padding: 0,
  },
}))

const Theming = () => {
  const theme = useTheme()
  const styles = useStyles(theme)

  return (
    <Layout>
      <SEO title="Theming" />
      <Sidebar />
      <Main>
        <section className={styles.callout}>
          <h1>Title</h1>
          <p>
            Theming control center provides you with all necessary details to customize and make
            Frames your own design system or prepare it for a new project.
          </p>
        </section>
        <h2>Colors</h2>
        <hr />
        <h3>Brand</h3>
        <ul className={styles.colors}>
          {Object.keys(theme.colors.brand).map((name) => (
            <li key={name}>
              <ThemeColorPicker color={theme.colors.brand[name]} name={name} />
            </li>
          ))}
        </ul>
      </Main>
    </Layout>
  )
}

export default Theming
